import axios from "axios";

const GET_KLAYTN_TOKENS_DATA_BY_WALLET_URL = `${process.env.REACT_APP_API_URL}/get-token-balances-by-wallet.php`;
const UPDATE_USER_COVER_IMAGE_URL = `${process.env.REACT_APP_API_URL}/update-user-cover-image.php`;
const UPDATE_USER_AVATAR_IMAGE_URL = `${process.env.REACT_APP_API_URL}/update-profile-image.php`;
const TRANSFER_NFT_URL = `${process.env.REACT_APP_NODE_JS_SERVER_URL}/nft-transfer`;

export function getKlaytnTokensDataByWallet(walletAddresses: Array<any>) {
    return axios({
        method: 'post',
        url: GET_KLAYTN_TOKENS_DATA_BY_WALLET_URL,
        headers: {
            'content-type': 'application/x-www-form-urlencoded',
        },
        data: {
            walletAddresses: JSON.stringify(walletAddresses)
        }
    })
}

export function updateUserCoverImage(formData: any) {
    return axios({
        method: 'post',
        url: UPDATE_USER_COVER_IMAGE_URL,
        headers: {
            'content-type': 'multipart/form-data',
        },
        data: formData
    })
}

export function updateUserAvatarImage(formData: any) {
    return axios({
        method: 'post',
        url: UPDATE_USER_AVATAR_IMAGE_URL,
        headers: {
            'content-type': 'multipart/form-data',
        },
        data: formData
    })
}

export const transferNFT = (
    collectionID: string,
    tier: string,
    custWalletAddress: string,
    userWalletAddress: string,
    userID: string,
    tokenIDs: Array<any>,
    blockchain: string,
    password: string
) => {
    return axios({
        method: 'post',
        url: TRANSFER_NFT_URL,
        headers: {
            'content-type': 'application/json',
        },
        data: {
            collectionID,
            tier,
            custWalletAddress,
            userWalletAddress,
            userID,
            tokenIDs,
            blockchain,
            password
        }
    })
}