import {useState, useEffect} from 'react'
import { useLocation } from 'react-router-dom'
import CircularProgress from "@mui/material/CircularProgress/CircularProgress";
import { getCollectorData, getWalletAddressesForUser } from './api';
import { TokenCard } from './components/TokenCard'
import { tokenBalancesForWallet, tokenBalancesForWallets } from '../dropDetail/dropDetailEthereum/queries/Queries'
import { getKlaytnTokensDataByWallet } from '../profile/crud';


const CollectorDetail: React.FC = () => {

    const [tokensMeta, setTokensMeta] = useState<any>([])
    const [collectorDetail, setCollectorDetail] = useState<any>(null)
    const [loading, setLoading] = useState<boolean>(true)
    const { state }: any = useLocation()
    const collectorID = state


    useEffect(() => {

        getCollectorData(collectorID)
        .then((response) => {
            if(response.data.success) {
                setCollectorDetail(response.data.collector_data)
            }
        })

        getWalletAddressesForUser(collectorID)
        .then((response) => {
            if(response.data.success) {
                let ethWallets = response.data.eth_addresses
                let klayWallets = response.data.klay_addresses

                tokenBalancesForWallets(ethWallets)
                .then((data) => {
                    setTokensMeta(data)
                })
                .then(() => {
                    getKlaytnTokensDataByWallet(klayWallets)
                    .then((response) => {
                        if(response.data.success) {
                            setTokensMeta((prevTokensMeta: any) => {
                                let newTokensMeta = [...prevTokensMeta, ...response.data.tokensMeta]
                                return newTokensMeta
                            })
                            setLoading(false)
                        }
                    })
                })
            }
        })
    }, [])

    const getCollectedNumber = (metaData: Array<any>) => {
        let numb = 0;
    
        metaData.map((data) => {
          if(data.balance > 0) {
            numb += 1
          }
        })
    
        return numb
    }


    return (
        <>
        {!loading &&
            <>
            <div className="bg-white" style={{ marginTop: '-30px' }}>
                <div className="position-relative m-auto w-100 h-300px">
                    <img 
                        style={{width: 'inherit', height: 'inherit'}} 
                        // src="https://newwallpapershd.com/wp-content/uploads/2018/11/Technology-Music-Beats-Guitar-Facebook-Covers-Photos.jpg"
                        src={
                            collectorDetail.collector_cover_image != 'default' ? `${process.env.REACT_APP_API_USER_COVER_IMAGE_URL}/${collectorDetail.collector_cover_image}` : "https://newwallpapershd.com/wp-content/uploads/2018/11/Technology-Music-Beats-Guitar-Facebook-Covers-Photos.jpg"
                        }
                        alt=""
                    />
                </div>
                <div className="d-flex m-auto w-100" style={{height: '130px'}}>
                    <div className="d-flex pb-0">
                        <div 
                            className="position-relative profile-position" 
                            style={{ width: '180px', height: '180px', borderRadius: '50%', marginLeft: '30px' }}
                        >
                            <img 
                                className="position-absolute" 
                                style={{ width: '180px', height: '180px', borderRadius: '50%' }}
                                // src="https://www.meme-arsenal.com/memes/100773de10bd652a2366e129c5053a0a.jpg" 
                                src={`${process.env.REACT_APP_API_USER_IMAGE_URL}/${collectorDetail.collector_image}`}
                                alt=""
                            />
                        
                        </div>
                    </div>
                    <div className='d-flex justify-content-between flex-wrap w-100'>
                        <div className="d-flex flex-column ms-3 mt-4">
                        <span className="fw-bolder" style={{ fontSize: '30px' }}>{collectorDetail.collector_name}</span>
                        {/* <span className="text-muted" style={{ fontSize: '20px' }}>{walletAddress}</span> */}
                        <span className="text-muted" style={{ fontSize: '20px' }}>COLLECTED : {getCollectedNumber(tokensMeta)}</span>
                        </div>
                        {/* <div className="d-flex flex-column py-4 px-4">
                        <span className="text-muted" style={{ fontSize: '20px' }}>COLLECTED</span>
                        <span className="text-muted" style={{ fontSize: '20px' }}>{tokensMeta.length}</span>
                        </div> */}
                    </div>
                </div>
                <hr className="mx-auto" style={{ width: '96%' }} />

                <div className="d-flex justify-content-center">
                {tokensMeta && tokensMeta.length == 0 &&
                    <div className="d-flex justify-content-center" style={{ height: '500px' }}>
                    <div className="py-5 my-auto">
                        <span className="fst-italic">NO ITEMS IN COLLECTION</span>
                    </div>
                    </div>
                }
                {tokensMeta && tokensMeta.length > 0 &&
                    <>
                    <div className='d-flex flex-wrap justify-content-center my-9'>
                        {tokensMeta.map((tokenMeta: any) => (
                            <>
                                {tokenMeta.balance > 0 && 
                                    <TokenCard 
                                        imagePath={`${process.env.REACT_APP_API_TOKEN_IMAGE_URL}/${tokenMeta.token_image}`}
                                        collectionID={tokenMeta.collection_id}
                                        tier={tokenMeta.token_tier}
                                        ownershipOffered={tokenMeta.ownership_offered}
                                        collectionName={tokenMeta.collection_name}
                                        dropDate={tokenMeta.drop_date}
                                        totalQuantity={tokenMeta.token_quantity}
                                        totalOwns={tokenMeta.balance}
                                        blockchain={tokenMeta.blockchain}
                                        artistName={tokenMeta.artist_name}
                                        collectorImage={collectorDetail.collector_image}
                                        collectorName={collectorDetail.collector_name}
                                    />
                                }
                            </>
                        ))}
                    </div>
                    </>
                }
                </div>
            </div>
            </>
        }
        {loading && 
            <div className='d-flex w-100 h-100'>
                <div className='my-auto mx-auto'>
                    <div className='d-flex flex-column justify-content-center'>
                        <span className='text-primary fs-3'>Loading</span> <br />
                        <span className='ms-4'>
                            <CircularProgress />
                        </span>
                    </div>
                </div>
            </div>
        }
        </>
    )
}

export default CollectorDetail
