import { DropdownCustomToggler } from 'app/modules/dropdown/DropdownCustomToggler';
import { DropdownMenu3 } from 'app/modules/dropdown/DropDownMenu3';
import { Dropdown } from 'react-bootstrap';
import { FormattedMessage } from 'react-intl';
import { useSelector, shallowEqual, useDispatch } from "react-redux";
import { useNavigate } from 'react-router-dom';
import {RootState} from "../../../../setup"



export const TokenCard = (props: any) => {

    const navigate = useNavigate()

    const renderGoldSection = () => {
        return (
            <figcaption 
                className="px-4 py-1" 
                style={{ background: 'linear-gradient(87.65deg, rgb(234, 209, 168) -1.34%, rgba(234, 209, 168, 0.2) 73.33%, rgb(234, 209, 168) 104.02%)' }}
            >
                <div className="d-flex justify-content-between">
                    <h6>GOLD</h6>
                    <h6>SINGLE</h6>
                </div>
            </figcaption>
        )
    }

    const renderPlatinumSection = () => {
        return (
            <figcaption 
                className="px-4 py-1" 
                style={{ background: 'linear-gradient(87.65deg, rgb(207, 216, 226) -1.34%, rgba(207, 216, 226, 0.2) 73.33%, rgb(207, 216, 226) 104.02%)' }}
            >
                <div className="d-flex justify-content-between">
                    <h6>PLATINUM</h6>
                    <h6>SINGLE</h6>
                </div>
            </figcaption>
        )
    }

    const renderDiamondSection = () => {
        return (
            <figcaption 
                className="px-4 py-1" 
                style={{ background: 'linear-gradient(87.65deg, rgb(193, 222, 228) -1.34%, rgba(193, 222, 228, 0.2) 73.33%, rgb(193, 222, 228) 104.02%)' }}
            >
                <div className="d-flex justify-content-between">
                <h6>Diamond</h6>
                <h6>SINGLE</h6>
                </div>
            </figcaption>
        )
    }
 
  return (
    <>
    <div 
        className="mx-3 my-3 cursor-pointer" 
        style={{ width: '280px' }}
    >
        <div 
            className="card card-custom gutter-b card-stretch product-card" 
            style={{ border: '1px solid rgb(184, 169, 169)' }}
            onClick={() => navigate('/collector-asset-detail', {state: {
                tierImage: props.imagePath,
                collectionName: props.collectionName,
                dropDate: props.dropDate,
                artistName: props.artistName,
                collectionID: props.collectionID,
                tier: props.tier,
                ownershipOffered: props.ownershipOffered,
                totalQuantity: props.totalQuantity,
                totalOwns: props.totalOwns,
                blockchain: props.blockchain,
                collectorName: props.collectorName,
                collectorImage: props.collectorImage
            }})}
        >
                <span>
                    <div className="text-center rounded">
                        <img src={props.imagePath} className="w-100" />
                    </div>
                    {props.tier == '0' && 
                        renderGoldSection()
                    }
                    {props.tier == '1' && 
                        renderPlatinumSection()
                    }
                    {props.tier == '2' && 
                        renderDiamondSection()
                    }
                    <div className='px-3 py-5'>
                        <div className="d-flex justify-content-between">
                            <div >
                                <h4 className="font-size-12">
                                    <div className="text-dark">{props.collectionName}</div>
                                </h4>
                                <h4 className="font-size-12">
                                    <div className="text-muted"><FormattedMessage id="PROFILE_PAGE.OWNS" />: {props.totalOwns} / {props.totalQuantity}</div>
                                </h4>
                            </div>
                            <div>
                                <h4>
                                    <div className="font-size-12"><FormattedMessage id="EXPLORE.SIDE_NAV.BLOCKCHAIN" /></div>
                                </h4>
                                <h4>
                                    <div className="font-size-12 text-muted">{props.blockchain == 'ethereum' ? 'Ethereum' : 'Klaytn'}</div>
                                </h4>
                            </div>
                        </div>
                    </div>
                </span>
            {/* </span> */}
        </div>
    </div>
    </>
  )
}
